<template>
    <div class="body_cunqu">
        <div class="content">
            <div class="card">
                <!-- <div class="top flex_align bor_b">
                    <div class="left"></div>
                    <div class="right flex_1"></div>
                </div> -->
                <div class="middle bor_b">
                    <div class="title"></div>
                    <div class="row flex_align">
                        <div></div>
                        <div class="ipt flex_1"><input type="tel" placeholder="" v-model="money"></div>
                    </div>
                </div>
                <div class="bottom">10000&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;1000</div>
            </div>
            <div class="com_btn" v-if="money%1000 == 0 && money>=10000"><div class="btn" @click="submit"></div></div>
            <div class="com_btn" v-else><div class="btn off"></div></div>
        </div>
        <!--  -->
        <transition name="wallet_fade">
            <div class="wallet_dialog" v-show="dialog.mask">
                <div class="mask"></div>
                <div class="table">
                    <div class="td">
                        <transition name="wallet_bounce">
                            <div class="container" v-show="dialog.con">
                                <div class="comfirm">
                                    <div class="text"></div>
                                    <div class="btns flex_align bor_t">
                                        <div class="btn no_btn flex_1"></div>
                                        <div class="btn yes_btn flex_1 bor_l"></div>
                                    </div>
                                </div>
                            </div>
                        </transition>
                    </div>
                </div>
            </div>
        </transition>
    </div>
</template>
<script>

export default {
    data(){
        return{
            switch:true,
            dialog:{
                mask:false,
                con:false
            },
            money:''
        }
    },
    mounted(){
        
    },
    methods:{
        submit(){
            let _this = this;

            if(!_this.switch) return false;
            _this.switch = false;

            var data = this.qs.stringify({
                token:localStorage.token,
                money: _this.money
            });
            _this.axios.post(_this.$store.state.walleturl+'/orders/buy',data).then(function(res){
                setTimeout(()=>{
                    _this.switch = true;
                },1500);
                if(res.data.code==1){
                    _this.$store.commit('msg', '');
                    setTimeout(()=>{
                        _this.$router.push({
                            path:'/wallet/chargeresult?money='+_this.money+'&name='
                        })
                    },1500);
                }else{
                    _this.$store.commit('msg', '');
                }
            });
        },
        //
        dislogShow(){
            let _this = this;
            _this.dialog.mask = true;
            setTimeout(() => {
                _this.dialog.con = true;
            }, 10);
        },
        dislogHide(){
            let _this = this;
            _this.dialog.con = false;
            setTimeout(() => {
                _this.dialog.mask = false;
            }, 10);
        }
    }
}
</script>



